.reels-container {
    background: var(--main-background);
    margin-bottom: 4rem;
    padding: 4rem;
}
.reels-heading {
    margin: 3rem;
    padding: 1rem 0 3rem 0;
}
.reels-heading h4 {
    font-size: 50px;
    font-weight: 800;
    font-family: var(--font-base);
    margin: 0;
}
.reels-heading p {
    font-size: 20px;
    font-weight: 600;
    padding-bottom: 4rem;
}
@media (max-width:500px){
    .reels-heading  {
    padding-bottom: 0;
    margin-bottom: 0;
}
    .reels-heading h4 {
        font-size: 40px;
        padding: 0;
    }
}